@mixin font-small-mobile {
  font-size: 12px;
  line-height: 16px;
}

@mixin font-middle-mobile {
  font-size: 14px;
  line-height: 24px;
}

@mixin font-large-mobile {
  font-size: 18px;
  line-height: 24px;
}

@mixin font-small-tablet {
  font-size: 14px;
  line-height: 24px;
}

@mixin font-middle-tablet {
  font-size: 14px;
  line-height: 24px;
}

@mixin font-large-tablet {
  font-size: 28px;
  line-height: 40px;
}